*, :after, :before {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  background-color: var(--main-background-color);
  font-family: var(--default-font-family);
  font-size: var(--default-font-size);
  justify-content: center;
  align-items: center;
  display: flex;
}

#game-board {
  gap: var(--cell-gap);
  grid-template-rows: repeat(var(--grid-size), var(--cell-size) );
  grid-template-columns: repeat(var(--grid-size), var(--cell-size) );
  padding: var(--cell-gap);
  background-color: var(--game-board-background-color);
  border-radius: var(--cell-gap);
  display: grid;
  overflow: auto;
}

.cell {
  background-color: var(--cell-background-color);
  border-radius: var(--cell-gap);
}

.tile {
  width: var(--cell-size);
  height: var(--cell-size);
  border-radius: var(--cell-gap);
  transform: translate( calc(var(--y)  * (var(--cell-size)  + var(--cell-gap) ) ), calc(var(--x)  * (var(--cell-size)  + var(--cell-gap) ) ) );
  z-index: 2;
  background: #fff;
  justify-content: center;
  align-items: center;
  transition: all .15s ease-in-out;
  animation-name: show;
  animation-duration: .2s;
  animation-timing-function: ease-in-out;
  display: flex;
  position: absolute;
  overflow: hidden;
}

@keyframes show {
  0% {
    opacity: 0;
    transform: translate( calc(var(--y)  * (var(--cell-size)  + var(--cell-gap) ) ), calc(var(--x)  * (var(--cell-size)  + var(--cell-gap) ) ) ) scale(0);
  }
}

